@import url(https://fonts.googleapis.com/css2?family=DM+Sans:wght@400;500;700&family=Poppins:ital,wght@0,200;0,300;0,400;0,500;0,600;0,700;0,800;1,500&display=swap);
*{
  padding: 0;
  margin: 0;
  margin-top: 0 !important;
  box-sizing: border-box;
  font-family: 'DM Sans', sans-serif;
  font-family: 'Poppins', sans-serif; 
  font-weight: 500;
}
.container90 {
  width: 90%;
  margin: 0 auto;
}

.margin-brand {
  width: 100px;
  text-align: center;
}
.margin-left-right {
  width: 150px;
  text-align: center;
}

.theme {
  color: rgb(80, 200, 220);
}

.box-shadow {
  border-radius: 10px;
  box-shadow: 0 6px 20px 0 rgba(0, 0, 0, 0.2);
}
.box {
  scale: 2rem;
}

.alignList {
  list-style: none;
  padding: 0;
}

.responsive {
  width: 100%;
  height: auto;
}
#carousel-img {
  height: 91vh; 
}
#custom-legend {
  background: none !important;
  color: white;
  opacity: 1;
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  -webkit-transform: translateX(-5%);
          transform: translateX(-5%);
  font-size: 30px;
  text-shadow: 1px 2px 20px rgb(0, 0, 0, 1), 1px 4px 30px rgb(0, 0, 0, 1);
}
#carousel-header {
  font-size: 50px;
}
.header {
  margin-top: 5% !important;
  margin-bottom: 5% !important;
  font-size: 2.8rem;
  font-weight: 600;
}

.btn-view {
  margin-top: 2rem !important;
}
.margin-top {
  margin-top: 3% !important;
}
.card-container {
  position: relative;
  transition: -webkit-transform 0.3s;
  transition: transform 0.3s;
  transition: transform 0.3s, -webkit-transform 0.3s;
  margin: 2vw !important;
}
.card-container:hover {
  -webkit-transform: translateY(-10px);
          transform: translateY(-10px);
}
.card-btn {
  margin-top: 5% !important;
  opacity: 0;
  transition: opacity 0.7s ease-in-out;
}
.card-container:hover .card-btn {
  opacity: 1;
}
